.wrapper
{
    position: absolute;
    top: 0;
    right: 0; 
    left: 0;
    bottom: 0;
  
    width: 75%;
    margin: auto;
  
    opacity: .3;
  
    -webkit-transform: translate(20%, 0);
  
            transform: translate(20%, 0);
}
.layer1
{
width: 60%;
height: 60%;
margin-top: 20%;
margin-bottom: 0%;
margin-left: 20%;
margin-right: 0%;
position: absolute;

}
.layer2{
width: 72.5%;
height: 70%;
margin-top: 15%;
margin-bottom: 0%;
margin-left: 15%;
margin-right: 0%;
position: absolute;
-webkit-transform-origin: 48.2% 50%;
        transform-origin: 48.2% 50%;
-webkit-animation:reverse_spin 40s linear infinite;
animation:reverse_spin 40s linear infinite;
}

.layer3{
    width: 85%;
    height: 80%;
    margin-top: 10%;
    margin-bottom: 0%;
    margin-left: 10%;
    margin-right: 0%;
    position: absolute;
    -webkit-transform-origin: 47.5% 50%;
            transform-origin: 47.5% 50%;
    -webkit-animation:spin 50s linear infinite;
    animation:spin 50s linear infinite;
}

.layer4{
    width: 94%;
    height: 90%;
    margin-top: 5%;
    margin-bottom: 0%;
    margin-left: 5%;
    margin-right: 0%;
    position: absolute;
    -webkit-transform-origin: 48.3% 50%;
            transform-origin: 48.3% 50%;
    -webkit-animation:reverse_spin 70s linear infinite;
    animation:reverse_spin 70s linear infinite;
}

@-webkit-keyframes spin { 
    100% { -webkit-transform: rotate(360deg); } 
}

@keyframes spin { 
    100% { 
        -webkit-transform: rotate(360deg); 
        transform:rotate(360deg); 
    } 
}

@-webkit-keyframes reverse_spin { 
    100% { -webkit-transform: rotate(-360deg); } 
}

@keyframes reverse_spin { 
    100% { 
        -webkit-transform: rotate(-360deg); 
        transform:rotate(-360deg); 
    } 
}
